export default HireDevopsEngineer;
export const queryData = graphql`
  query HireDevopsEngineerPageQuery {
    HireDevopsEngineerImg: file(
      relativePath: { eq: "hire-devops-engineer.png" }
    ) {
      childImageSharp {
        gatsbyImageData
      }
    }
    heroImage: file(
      relativePath: { eq: "hirescreens/devPages/devOps-engineer-hero.webp" }
    ) {
      childImageSharp {
        gatsbyImageData(
          layout: CONSTRAINED
          width: 1200
          quality: 90
          placeholder: BLURRED
        )
      }
    }

    site {
      siteMetadata {
        firestore {
          apiKey
          appId
          authDomain
          messagingSenderId
          projectId
          storageBucket
        }
      }
    }

    allMetaJson(filter: { slug: { eq: "hire-devops-engineer" } }) {
      nodes {
        metaTitle
        metaKeyword
        title
        slug
        description
      }
    }
  }
`;
